import { h } from 'preact';
import BoltToolkitApp from 'base/boltToolkit/boltToolkitApp.js';

const BoltToolkitDoc = ({ appProps, initialState, appData, relativeRoot, reload, ...rest }) => {

	let initialData = (
		<script
			dangerouslySetInnerHTML={{ __html: `
window.__INITIAL_STATE__ = JSON.parse('${JSON.stringify(initialState).replace(/[\u2028\u2029]/g, '').replace(/(['\\])/g, '\\$1')}');
window.__BUILD_DATE__ = ${Date.now()};` }}
		/>
	);

	const title = initialState?.toolkitData?.title || `${appData?.meta?.title?.locale}: Powster Platform Toolkit`;

	const buildDate = Date.now();

	const favicon = appData?.doc?.metaTags?.favicon;
	const faviconIco = `${favicon?.ico || 'favicon.ico'}?v=${buildDate}`;
	const faviconPng = `${favicon?.png || 'favicon.png'}?v=${buildDate}`;

	return (
		<html>
			<head>
				<title>{title}</title>
				<meta charset="UTF-8" />
				<meta name="viewport" content="minimal-ui, initial-scale=1" />
				<meta name="robots" content="noindex" />
				<link rel="stylesheet" href="https://use.typekit.net/gsb3nxg.css" />
				<link rel="stylesheet" type="text/css" href={`${relativeRoot}css/boltToolkit.css`} />
				<link rel="shortcut icon" type="image/png" href={faviconPng} />
				<link rel="icon" type="image/x-icon" href={faviconIco} />
			</head>
			<body>
				<div id="app">
					<BoltToolkitApp {...appProps} />
				</div>
				{initialData}
				{reload}
				<script src={`${relativeRoot}js/boltToolkit.js`} charset="utf-8" />
			</body>
		</html>
	);
};


export default BoltToolkitDoc;
